import { VueEditor } from 'vue2-editor'
import { mapState } from 'vuex'

export default {
  data(){
    return {
      post: {
        year: "2017",
        established_time: new Date().toLocaleDateString(),
        short_description: "",

      },
      create_mode: false
    }
  },
  mounted(){
    if (this.$route.params.id){
      this.axios.get("/api/spa/post/"+this.$route.params.id).then(res=>{
        this.post=res.data
      })
    }else{
      this.create_mode=true
    }
  },
  components:{
    VueEditor
  },
  methods: {
    handleSave(){
      if (!this.create_mode){
        this.axios.patch(
          "/api/spa/post/"+this.$route.params.id,
          this.post
        ).then(res=>{
          console.log(res.data)
          if (res.data.status=="success"){
            this.$message({
              message: '儲存成功',
              type: 'success'
            });
          }else{
            
            this.$message.error({
              message: '儲存失敗',
              // type: 'error'
            });
  
          }
        })

      }else{
        this.axios.post(
          "/api/spa/post/",
          this.post
        ).then(res=>{

          this.$message({
            message: '建立成功',
            type: 'success'
          });
          // let _this  = this
          console.log(res.data.data.id)
          // setTimeout(function(){
          this.$router.push("/post/"+res.data.data.id)
          // },300)
        })

      }
    }
  },
  computed: {
    ...mapState(['companies','catas']),
    year_catas(){
      return this.catas.filter(o=>o.year==this.post.year)
    }
  }
}



